/*purgecss start ignore*/

html {
  /*SCROLL SNAP*/
  /*Redundancy due to ensuring that scroll snap works on all browsers */
  /* scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
  scroll-margin: 50px 0 0 50px; */
  scroll-behavior: smooth;
  /*GENERAL STYLING*/
  font-family: sans-serif;
  cursor: url("https://upload.wikimedia.org/wikipedia/commons/thumb/d/da/White_dot.svg/16px-White_dot.svg.png"), auto;
}

/* Hide scrollbar for Chrome, Safari and Opera */

body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */

body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

section {
  height: 100vh;
  /* Scroll snap setup for section */
  /* scroll-snap-align: start; */
}

.flexTextCenter {
  display: flex;
  justify-content: center; /* Align horizontal */
  align-items: center; /* Align vertical */
}

/* NAVBAR DOTS */

.dot {
  height: 3rem;
  width: 5px;
  background-color: #fff;
  /* border-radius: 50%; */
  display: inline-block;
  opacity: 0.5;
  transition: all 0.4s;
  border: #fff;
  border: 1em;
}

.dot.active {
  height:3rem;
  width: 10px;
  opacity: 1;
}

/* GENERAL VERTICAL CENTER */

.vertical-center {
  margin: 0;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

/* LOADING SCREEN */

.loadingScreen {
  align-items: center;
  justify-content: center;
}

/* TRACKING EYEBALLS */

.eyes{
  position:absolute;
}

.eye{
  width:240px;
  height: 120px;
  background: #fff;
  display: inline-block;
  border-radius: 50%;
  position: relative;
  /* Trick to "masking" */
  overflow: hidden;
}

.ball{
  width : 70px;
  height : 70px;
  background: #000;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border: 10px solid #333;
}

/* LAGGING CURSOR */

.laggingCursor {
  pointer-events: none;
  z-index: 100;
  position: absolute;
  border: solid 1px #ccc;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transform: translate(10%,10%);
}

/* SIGNATURE */

.autographPath {
  opacity: 1;
}

.autographPath.animated {
  animation : line 1s ease-out forwards;
}

@keyframes line {
  100% {
    stroke-dashoffset: 0;
  }
}

/* FOOTER */

.footer {
  font-size: medium;
  height: 20rem;
  background-color: rgba(30, 30 ,30 , 0.5);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}

/* Home Navigation */

.homenav {
  opacity: 50%;
  transition: 0.3s;
  border-style: none none none solid;
  border-width:0px;
  padding: 5px;
}

.homenav:hover {
  opacity: 100%;
  border-width:0.5rem;
}

.homenavBottom {
  opacity: 50%;
  transition: 0.3s;
  border-style: none none none solid;
  border-width:0px;
  padding: 5px;
}

.homenavBottom:hover {
  opacity: 100%;
  border-width:0.5rem;
}

/* Sticky Slides */

.stacking-slide {
  width: 100%;
  /* position: sticky; UNCOMMENT FOR STACKING SLIDES EFFECT*/
  top: 0;
}

/* Gallery */

.gallery {
  display: flex;
  padding: 2px;
  transition: .3s;
}

.gallery:hover .gallery__image{
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}

.gallery__image:hover {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}

.gallery__column {
  display: flex;
  flex-direction: column;
  width: 25%;
}

.gallery__link {
  margin: 2px;
  overflow: hidden;
}

.gallery__link:hover .gallery__image {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}

.gallery__link:hover .gallery__caption {
  opacity: 1;
}

.gallery__thumb {
  position: relative;
}

.gallery__image {
  display: block;
  width: 100%;
  transition: .3s;
}

.gallery__image:hover {
  transform: scale(1.1);
}

.gallery__caption {
  position: absolute;
  bottom: 0;
  left: 0;
