  50% {
    -webkit-clip-path: polygon(100% 5%, 100% 68%, 56% 85%, 0 73%, 0 39%, 55% 72%);
    clip-path: polygon(100% 5%, 100% 68%, 56% 85%, 0 73%, 0 39%, 55% 72%);
  }

  100% {
    -webkit-clip-path: polygon(100% 5%, 100% 86%, 27% 100%, 0 73%, 0 39%, 28% 90%);
